import Services from "./Services";

class UtilService extends Services {
  constructor(init) {
    super(init);
    this._name = "UTIL";
    return this;
  }

  /**
   * @function find - Gets one or many users (**Admins only**)
   * @param {Object} params
   * @returns
   */
  find = async (params) => {
    return await this.decorate(
      async () =>
        await this.axios(`util`, {
          method: "GET",
          params,
        })
    );
  };
  /**
   * @function findByID
   * @param {String} id
   * @param {Object} params
   * @returns
   */
  findByType = async (type, params) => {
    return await this.decorate(
      async () =>
        await this.axios(`util/${type}`, {
          method: "GET",
          params,
        })
    );
  };
}

export default UtilService;
