import Services from "./Services";

export class AuthService extends Services {
  constructor(init) {
    super(init);
    this._name = "AUTH";
    return this;
  }

  //LOGIN ----------------------------------------------------------------------
  /**
   * @function login - log user to platform
   * @param {Object} data
   * @param {String} data.email
   * @param {String} data.password
   * @param {Number | 1} data.access_level
   * @returns
   */
  login = async (data) => {
    return await this.decorate(
      async () =>
        await this.axios("/auth/login", {
          method: "POST",
          data,
        })
    );
  };

  //REGISTER ----------------------------------------------------------------------
  /**
   * @function register - Register user into platform
   * @param {Object} data
   * @param {String} data.email
   * @param {String} data.invite_code
   * @param {String} data.password
   * @param {String} data.repeat_password
   * @returns
   */
  register = async (data) => {
    return await this.decorate(
      async () =>
        await this.axios(`/auth/register`, {
          method: "POST",
          data,
        })
    );
  };

  //CONFIRM USER ----------------------------------------------------------------------
  /**
   * @function confirm - Confirm user in to the  platform
   * @param {Object} data
   * @param {String} data.email
   * @param {String} data.token
   * @returns
   */
  confirm = async (data) => {
    return await this.decorate(
      async () =>
        await this.axios(`/auth/confirm`, {
          method: "POST",
          data,
        })
    );
  };

  /**
   * @function resendConfirmationMail - Confirm user in to the  platform
   * @param {Object} data
   * @param {String} data.email - user email
   * @returns
   */
  resendConfirmationMail = async (data) => {
    return await this.decorate(
      async () =>
        await this.axios(`/auth/confirm/resend`, {
          method: "POST",
          data,
        })
    );
  };

  //RESET USER ----------------------------------------------------------------------
  /**
   * @function reset
   * @description reset password
   * @param {Object} data
   * @param {Object} data.token
   * @param {Object} data.password
   * @returns
   */
  reset = async (data) => {
    return await this.decorate(async () => {
      await this.axios(`/auth/reset`, {
        method: "POST",
        data,
      });
    });
  };

  //PASSWORD RESET REQUEST ----------------------------------------------------------------------
  /**
   * @function request_reset
   * @description request password reset
   * @param {Object} data
   * @param {String} data.email
   * @returns
   */

  request_reset = async (data) => {
    return await this.decorate(async () => {
      await this.axios(`/auth/reset/request`, {
        method: "POST",
        data,
      });
    });
  };

  //SEND OTP ----------------------------------------------------------------------
  /**
   * @function send_otp - Send otp to verify user
   * @param {Object} data
   * @param {String} data.id - user ID
   * @returns
   */

  send_otp = async (data) => {
    return await this.decorate(async () => {
      await this.axios(`/auth/otp`, {
        method: "POST",
        data,
      });
    });
  };

  //VERIFY OTP ----------------------------------------------------------------------
  /**
   * @function verify_otp
   * @description verify OTP
   * @param {Object} data
   * @param {String} data.id
   * @param {String} data.code
   * @returns
   */
  verify_otp = async (data) => {
    return await this.decorate(async () => {
      await this.axios(`/auth/verify`, {
        method: "POST",
        data,
      });
    });
  };
}
export default AuthService;
